import {AfterViewInit, Component} from '@angular/core';
import {ProfileService} from '../shared';
import {environment} from '../../environments/environment';
import {NavigationEnd, Router} from '@angular/router';

@Component({
    selector: 'app-logout',
    templateUrl: 'logout.page.html',
    styleUrls: [],
})
export class LogoutPage implements AfterViewInit {
    public appVersion = environment.version;

    constructor(
        private router: Router,
        private profile: ProfileService
    ) {
        this.router.events.subscribe(async (val) => {
            if (val instanceof NavigationEnd && this.router.url.indexOf('/logout') > -1) {
                await this.profile.logout(environment.fidjKey, environment.production);
                await this.profile.gotoLogin();
            }
        });
    }

    async ngAfterViewInit() {}
}
